*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

/* .summary */

.summary {
    display: inline-block;
    position: relative;
    overflow: hidden;
    border: 1px solid black;
    border-radius: 20px;
    margin: 10px;
    width: 95%;
    align-items: center;
    background-color: #ffff;
    color: #000000;
    padding: 10px;
    margin-bottom: 30px;
    transition: 0.5s ease;
    z-index: 0;
}

.github-link {
    margin-top: 25px;
    height: 50px;
    width: 100%;
    position: absolute;
    background-color: transparent;
    color: white;
    z-index:3;
}

.github-link:hover ~ .summary {
    background-color: #282c34;
    height: 7em;
    color: #282c34;
}

.github-link:before,
.github-link:after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    color: white;
    font-size: 50px;
    text-align: center;
    opacity: 0;
    -webkit-transition: .4s,opacity .6s;
    -moz-transition: .4s,opacity .6s;
    -o-transition: .4s,opacity .6s;
    transition: .4s,opacity .6s;
    z-index: 11;
}

.github-link:before {
    content: attr(data-hover);
    -webkit-transform: translate(0,0);
    -moz-transform: translate(0,0);
    -ms-transform: translate(0,0);
    -o-transform: translate(0,0);
    transform: translate(0,0);
}

.github-link:hover:before {
    opacity: 1;
    -webkit-transform: translate(0,0);
    -moz-transform: translate(0,0);
    -ms-transform: translate(0,0);
    -o-transform: translate(0,0);
    transform: translate(0,0);
    -webkit-transition-delay: .4s;
    -moz-transition-delay: .4s;
    -o-transition-delay: .4s;
    transition-delay: .4s;
}


#work {
    animation:work 5s infinite
}

@keyframes work {
    0% {background-color: #607d8b; color: white}
    50% {background-color: #795548;}
    100% {background-color: #607d8b; color: white}
}

#resume {
    animation:resume 30s infinite
}

@keyframes resume {
    0% {background-color: #000; color:white}
    50% {background-color: #607d8b; color: white}
    100% {background-color: #000; color:white}
}

#extracurricular {
    animation:extracurricular 7s infinite
}

@keyframes extracurricular {
    0% {background-color: #616161; color: white}
    30% {background-color: #607d8b; color: white}
    60% {background-color: #795548;}
    100% {background-color: #616161; color: white}
}

#contact {
    animation:contact 10s infinite
}

@keyframes contact {
    0% {background-color: #616161; color: white}
    50% {background-color: #D3D3D3; color: black}
    100% {background-color: #616161; color: white}
}

#about {
    animation:about 20s infinite
}

@keyframes about {
    0% {background-color: #D3D3D3; color: black}
    50% {background-color: #607d8b; color: white}
    100% {background-color: #D3D3D3; color: black}
}